@import '../../scss/variables';

.sort {
	position: relative;

	@media (max-width: 550px) {
		margin-bottom: 20px;
	}

	&__label {
		display: flex;
		align-items: center;

		svg {
			margin-right: 8px;
			path {
				fill: #fe5f1e;
				height: 15px;
				stroke-width: 1px;
			}
		}

		b {
			width: 110px;
			margin-right: 8px;
		}

		span {
			display: flex;
			align-items: center;
			justify-content: space-between;
			gap: 5px;
			color: $orange;
			cursor: pointer;
			-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
		}
	}

	&__popup {
		position: absolute;
		right: 0;
		margin-top: 15px;
		background: #ffffff;
		box-shadow: 0px 5px 15px rgba(0.5, 0.5, 0.5, 0.5);
		border-radius: 10px;
		overflow: hidden;
		padding: 10px 0;
		width: 170px;
		animation: appear 0.6s;

		@keyframes appear {
			from {
				opacity: 0;
			}

			to {
				opacity: 1;
			}
		}

		ul {
			overflow: hidden;
			li {
				display: flex;
				align-items: center;
				gap: 5px;
				padding: 12px 20px;
				cursor: pointer;
				-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
				transition: background-color 0.5s ease;
				svg {
					height: 15px;
					stroke-width: 1px;
				}
				&:hover {
					background-color: rgba(254, 95, 30, 0.5);
				}
			}
		}
	}

	&__active {
		font-weight: bold;
		color: $orange;
	}
}
