.cart__empty {
	margin: 0 auto;
	max-width: 560px;
	text-align: center;
	padding: 0 30px;

	h2 {
		font-size: 32px;
		font-weight: 700;
		margin-bottom: 10px;
		span {
			display: block;
			margin-top: 10px;
		}
	}

	p {
		display: block;
		font-size: 20px;
		line-height: 145.4%;
		letter-spacing: 0.01em;
		color: #777777;
		
	}

	img {
		display: block;
		width: 60%;
		margin: 45px auto 60px;
	}

}
