@import '../../scss/variables';

.header {
	border-bottom: 1px solid $gray-line;
	padding: 40px 0;

	.container {
		width: $container-width;
		margin: 0 auto;
		display: flex;
		align-items: center;
		justify-content: space-between;

		@media (max-width: 720px) {
			width: 95%;
			flex-direction: column;
			gap: 15px;
		}
	}

	&__logo {
		display: flex;

		img {
			margin-right: 15px;
			margin-bottom: 4px;
		}

		h1 {
			color: #181818;
			font-size: 24px;
			letter-spacing: 1%;
			text-transform: uppercase;
			font-weight: 800;
			user-select: none;
			margin-bottom: 3px;
		}

		p {
			font-weight: 800;
			color: #7b7b7b;
		}
	}
	@media (max-width: 685px) {
		padding: 10px 0;

	}
}
