.pizza_block {
  width: 280px;
  text-align: center;
  margin-bottom: 65px;
  @media (max-width: 550px){
    margin-bottom: 30px;
  }

  img {
    width: 260px;
    height: 260px;
    object-fit: cover;

    @media (max-width: 550px){
      width: 200px;
      height: 200px;
    }
  }

  h4 {
    font-size: 20px;
    font-weight: 900;
    letter-spacing: 1px;
    margin-bottom: 20px;
    @media (max-width: 550px){
      margin-bottom: 10px;
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
  }

  &__price {
    font-weight: bold;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.015em;
  }
}
