.search {
	position: relative;

	@media (max-width: 1220px) {
		left: 0;
	}

	input {
		border: 1px solid rgba(0, 0, 0, 0.1);
		padding: 15px 15px;
		padding-left: 45px;
		width: 350px;
		border-radius: 20px;
		font-size: 16px;
		box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
		&:focus {
			border: 1px solid #e9a68b;
		}
		@media (max-width: 648px) {
			padding: 7px 15px;
			padding-left: 50px ;
		}
	}
	.icon {
		width: 24px;
		opacity: 0.3;
		position: absolute;
		top: 50%;
		transform: translateY(-45%);
		left: 14px;
	}

	.close {
		width: 20px;
		opacity: 0.3;
		position: absolute;
		top: 50%;
		transform: translateY(-45%);
		right: 14px;
		cursor: pointer;
		&:hover {
			opacity: 0.7;
		}
	}

	@media (max-width: 970px) {
		left: 0;
		input {
			width: 192px;
		}
	}
}
