@import '../../scss/variables';

.cart__item {
	display: flex;
	width: 100%;
	border-radius: 8px;
	border: 2px solid lighten($orange, 30);
	padding: 20px;
	margin-top: 20px;
	box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px,
		rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;

	@media (max-width: 650px) {
		flex-direction: column;
		align-items: center;
		gap: 30px;
	}

	@media (max-width: 660px) {
		gap: 15px;
	}

	&_img {
		display: flex;
		align-items: center;
		margin-right: 15px;
		width: 10%;
		@media (max-width: 890px) {
			display: none;
		}

		img {
			width: 80px;
			height: 80px;
		}
	}

	&_info {
		display: flex;
		flex-direction: column;
		justify-content: center;
		width: 40%;

		@media (max-width: 650px) {
			width: 90%;
			text-align: center;
		}

		h3 {
			font-weight: bold;
			font-size: 22px;
			line-height: 27px;
			letter-spacing: 0.01em;
		}

		p {
			font-size: 18px;
			color: #614d4d;
		}
	}

	&_count {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 15%;
		user-select: none;

		@media (max-width: 650px) {
			justify-content: center;
			width: 40%;
		}

		@media (max-width: 660px) {
			gap: 20px;
		}

		@media (max-width: 890px) {
			width: 20%;
		}

		svg {
			cursor: pointer;
			-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
			width: 31px;
			height: 31px;
			border-radius: 100%;
			box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px,
				rgba(0, 0, 0, 0.3) 0px 7px 13px -3px,
				rgba(0, 0, 0, 0.2) 0px -3px 0px inset;

			path {
				fill: darken($color: $orange, $amount: 0);
				transition: fill 0.4s ease-in-out;
			}

			&:hover,
			&:active {
				path {
					fill: darken($color: $orange, $amount: 20);
				}
			}
			&:active {
				transform: translateY(1px);
			}
		}

		.minus_disabled {
			svg {
				cursor: auto;
				transform: none;
				path {
					fill: darken($color: $gray-line, $amount: 7);
				}
			}
		}

		b {
			font-size: 22px;
		}
	}

	&_price {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 33%;

		@media (max-width: 650px) {
			justify-content: space-around;
			width: 100%;
		}

		b {
			font-weight: bold;
			font-size: 22px;
			letter-spacing: 0.01em;
		}
	}

	&_remove {
		display: flex;
		align-items: center;
		justify-content: flex-end;
		cursor: pointer;
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
		
		svg {
			width: 31px;
			height: 31px;
			transform: rotate(45deg);
			border-radius: 100%;
			box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px,
				rgba(0, 0, 0, 0.3) 0px 7px 13px -3px,
				rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
			background-color: rgb(236, 12, 12);
			transition: all 0.6s ease-in-out;
			opacity: 0.7;
			path {
				fill: darken($color: $gray-line, $amount: 5);
				;
			}

			&:hover,
			&:active {
				opacity: 1;
			}
		}
	}
}
