@import '../../scss/variables';

.categories {
	ul {
		display: flex;
		flex-wrap: wrap;

		@media (max-width: 1240px) {
			gap: 20px;
		}

		@media (max-width: 720px) {
			align-items: baseline;
			justify-content: space-around;
			align-content: center;
		}

		li {
			background-color: #f2f0f0;
			padding: 13px 30px;
			border-radius: 30px;
			margin-right: 10px;
			font-weight: bold;
			-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
			cursor: pointer;
			box-shadow: rgba(0, 0, 0, 0.27) 0px -23px 25px 0px inset;
			transition: background-color 0.5s ease-in-out;

			&:hover {
				background-color: darken(#f3eaea, 10%);
			}

			&:active {
				background-color: darken(#f3eaea, 5%);
			}

			&.active {
				background-color: #282828;
				color: #fff;
				box-shadow: rgba(240, 237, 237, 0.17) 0px -13px 15px 0px inset;
			}
			@media (max-width: 685px) {
				padding: 4px 8px;
				gap: 10px;
			}
		}
		@media (max-width: 685px) {
			padding: 4px 8px;
			gap: 10px;
		}
	}
}
