@import '../../../scss/variables';

.modal {
	position: fixed;
	z-index: 1;
	padding-top: 200px;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;

	background-color: rgba(0, 0, 0, 0.6);
	overflow-y: hidden;

	&_content {
		background-color: #C8C7C7;
		margin: 0 auto;
		padding: 20px;
		width: 450px;
		border-radius: 10px;
    box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
		@media (max-width: 768px) {
			width: 300px;
		}

		h3 {
			font-size: 20px;
			color: $orange;
			text-align: center;
		}
		.buttons {
			margin-top: 40px;
			display: flex;
      gap: 80px;
			justify-content: center;
			button {
				background-color: #21540c;
				padding: 10px;
				box-shadow: rgba(50, 50, 93, 0.55) 0px 50px 100px -20px,
					rgba(0, 0, 0, 0.6) 0px 30px 60px -30px,
					rgba(10, 37, 64, 0.35) 0px -5px 6px 4px inset;
          &:hover{
            opacity: 0.8;
          }

				span {
					padding: 10px;
					font-size: 16px;
					font-weight: 800;
				}
				&:first-of-type {
					background-color: #c30104;
				}
			}
		}
	}
	// :global {
	// 	body {
	// 		position: fixed;
	// 	}
	// }
}
