
  .pizza__selector {
    display: flex;
    background-color: #f3eaea;
    border-radius: 10px;
    flex-direction: column;
    padding: 6px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;

    ul {
      display: flex;
      flex: 1;
      

      &:first-of-type {
        margin-bottom: 6px;
      }

      li {
        padding: 8px;
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
        flex: 1;
        cursor: pointer;
        font-weight: 600;
        font-size: 14px;
        text-align: center;
      }
    }

  &__active {
    background:  #ffffff;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.14);
    border-radius: 5px;
    cursor: auto;
    
  }
}
